<template>
  <div id="wine_lights">
    <!-- 头部 -->
    <common-header :bg="commonHeaderBg">
      <div slot="left" class="common_header_left" @click="goBack">
        <i class="icon iconfont">&#xe68f;</i>
      </div>
      <div slot="center" class="common_header_center oh-1">{{ $t('lang.ambient_mode') }}</div>
    </common-header>
    <!-- 主体 -->
    <div class="lights_tab_card_content">
      <!-- shele light -->
      <div>
        <div class="lights_content">
          <div class="lights_switch clearfix">
            <p class="lights_switch_title oh-1">{{ $t('lang.shelf_lights')}}</p>
            <div class="lights_switch_btn" v-show="false">
              <van-switch
                v-model="lightsAmbientOnoff"
                active-color="#000"
                inactive-color="#fff"
                @change="shelfLightsSwitch"
                :disabled="RemoteControlModeFlag==0 || sabbathmode == 1 || onlineStatus == '2'"
                @click="toToast"
              />
            </div>
          </div>
          <div class="lights_item">
            <div style="display:flex">
              <p class="lights_title">{{ $t('lang.brightness') }}</p>
              <p
                class="lights_bright_percent"
                :style="{color:(conShow?'#000':'#c4c4c4')}"
                v-if="AmbientLightsIntensity > 0"
              >{{AmbientLightsIntensity }}%</p>
              <p
                class="lights_bright_percent"
                :style="{color:(conShow?'#000':'#c4c4c4')}"
                v-else
              >{{ $t('lang.off') }}</p>
            </div>
            <div
              v-if="conShow"
              class="lights_slider_wrap"
              :style="brightActive ? sliderBgSelect : sliderBg"
            >
              <van-slider
                v-model="AmbientLightsIntensity"
                :bar-height="barHeight"
                inactive-color="none"
                active-color="none"
                :step="step"
                :min="minVal"
                :max="maxVal"
                @drag-start="dragFlag = true"
                @drag-end="handleShelfLightsBrightnessDrag"
                @change="handleShelfLightsBrightness"
              >
                <div
                  slot="button"
                  class="lights_button"
                  :style="{ background: brightActive ? '#fff' : '#000' }"
                ></div>
              </van-slider>
            </div>
            <div v-if="!conShow" class="lights_disable_block"></div>
            <!-- <p class="lights_bright_percent_show" style="{color:'#000}" :style="{color:(conShow?'#000':'#c4c4c4')}"> -->
            <p class="lights_bright_percent_show" style="color:#000">
              <span>{{ $t('lang.off') }}</span>
              <span>100%</span>
            </p>
          </div>
          <!-- light_temperature -->
          <div class="lights_item">
            <p class="lights_title">{{ $t('lang.light_temperature') }}</p>
            <div class="lights_slider_wrap" :style="sliderBgs">
              <van-slider
                v-model="AmbientLightsColour"
                :bar-height="barHeight"
                inactive-color="none"
                active-color="none"
                :step="step"
                :min="minLight"
                :max="maxLight"
                @drag-start="dragFlag = true"
                @drag-end="handleLightsTemperatureDrag"
                @change="handleLightsTemperature"
              >
                <div
                  slot="button"
                  class="lights_button"
                  :style="{ background: lTActive ? '#fff' : '#000' }"
                ></div>
              </van-slider>
            </div>
            <!-- <div v-if="!conShow" class="lights_disable_block"></div> -->
            <!-- <p class="lights_bright_percent_show" :style="{color:(conShow?'#000':'#c4c4c4')}"> -->
            <p class="lights_bright_percent_show" style="color:#000">
              <span>{{ $t('lang.warm') }}</span>
              <span>{{ $t('lang.cold') }}</span>
            </p>
          </div>
          <br />
          <template v-if="oneZoneFlag == 0">
            <div class="line"></div>
            <!-- RGB -->
            <div class="lights_content">
              <div class="lights_switch clearfix">
                <p class="lights_switch_title oh-1">{{ $t('lang.rgb_light')}}</p>
                <div class="lights_switch_btn" v-show="false">
                  <van-switch
                    v-model="RGBLightOnoff"
                    active-color="#000"
                    inactive-color="#fff"
                    @change="RGBlightsSwitch"
                    :disabled="RemoteControlModeFlag==0 || sabbathmode == 1|| onlineStatus == '2'"
                    @click="toToast"
                  />
                </div>
              </div>
              <div class="lights_item">
                <div style="display:flex">
                  <p class="lights_title">{{ $t('lang.brightness') }}</p>
                  <p class="lights_bright_percent" v-if="AmbientRGBlightIntensity > 0">{{AmbientRGBlightIntensity}}%</p>
                  <p class="lights_bright_percent" v-else>{{$t('lang.off')}}</p>
                </div>
                <div class="lights_slider_wrap" :style="brightActive ? sliderBgSelect : sliderBg">
                  <van-slider
                    v-model="AmbientRGBlightIntensity"
                    :bar-height="barHeight"
                    inactive-color="none"
                    active-color="none"
                    :step="step"
                    :min="minVal"
                    :max="maxVal"
                    @drag-start="dragFlag = true"
                    @drag-end="handleRGBLightBrightnessDrag"
                    @change="handleRGBLightBrightness"
                  >
                    <div
                      slot="button"
                      class="lights_button"
                      :style="{ background: brightActive ? '#fff' : '#000' }"
                    ></div>
                  </van-slider>
                </div>
                <!-- <div class="lights_disable_block"></div> -->
                <!-- <p class="lights_bright_percent_show" :style="{color:(conShow?'#000':'#c4c4c4')}"> -->
                <p class="lights_bright_percent_show" style="color:#000">
                  <span>{{$t('lang.off')}}</span>
                  <span>100%</span>
                </p>
              </div>
            </div>

            <div class="lights_item">
              <p class="lights_title">{{ $t('lang.rgb_lights_colour') }}</p>
              <div class="ambient_color" @click="selectColor" :style="{background:_AmbientColour}"></div>
            </div>
          </template>
          <div class="line"></div>
          <div class="lights_item onTime" @click="togglePop">
            <p class="lights_title">{{ $t('lang.on_for') }}</p>
            <span>{{AmbientOntimeText}}</span>
            <!-- <input type="text" :value="AmbientOntimeOntime" > -->
          </div>
          <van-popup
            v-model="onforPop"
            position="bottom"
            :style="{ height: 100 }"
            @click-overlay="showPopSave"
          >
            <van-picker
              :columns="presenseSensorDisplayList"
              :confirm-button-text="$t('lang.confirm')"
              :cancel-button-text="$t('lang.cancel')"
              show-toolbar
              @cancel="onCancel"
              @confirm="onConfirm"
            />
          </van-popup>
        </div>
      </div>
    </div>
    <common-bottom :text="$t('lang.save')" @btnHandle="saveHandle" v-if="!showPop"></common-bottom>
    <!-- loading -->
    <loading :show-loading="showLoading"></loading>
  </div>
</template>
<script type="text/ecmascript-6">
import { Switch, Slider } from "vant";
export default {
  name: "lights",
  data() {
    return {
      show: true,
      lan: "en",
      hasLanguage: false,
      from: "html",
      deviceId: "",
      nativeKey: "lights",
      // items: [ 'switch', 'light_modes' ],
      // curId: 0,
      lightsInfo: "",
      lightsAmbientOnoff: true, // shelf lights切换
      conShow: true, //
      conShow_rgb: false,
      brightVal: 0, // brightness显示值
      lightVal: 0, // 环境色
      showLighta: 0, //调节
      ambientalAmbient: true,
      amShow: false,
      ambientVal: 0,
      // ambientColor: this.$store.state.lights.ambientColor,
      // ambientRcolour: "",
      // ambientGcolour: "",
      // ambientBcolour: "",
      showLightb: 0,
      step: 1,
      minVal: 0,
      maxVal: 100,
      minLight: 0,
      maxLight: 23,
      brightActive: false,
      lTActive: false,
      ambientActive: false,
      showLoading: false,
      AmbientLightsIntensity: 0, // 灯架亮度
      AmbientLightsColour: 0, //灯架颜色
      AmbientRGBlightIntensity: 0, //RGB亮度
      AmbientColour: this.$store.state.lights.AmbientlColour, //下面三个颜色的拼接值
      AmbientGColour: "",
      AmbientRColour: "",
      AmbientBColour: "",
      AmbientOntime: 0, //RGB持续时间,
      RGBLightOnoff: false, // RGB灯开关
      showPop: false, //显示遮罩层
      // 0 5min
      // 1 15min
      // 2 30min
      // 3 60min
      // 4 90min
      // 5 alwals
      // 6 1min 新增 但是顺序要按照 1 - always排
      presenseSensorDisplayList: [
        this.$t("lang.after_one_minute"),
        this.$t("lang.after_five_minute"),
        this.$t("lang.after_fifteen_minute"),
        this.$t("lang.after_thirty_minute"),
        this.$t("lang.after_sixty_minute"),
        this.$t("lang.after_ninety_minute"),
        this.$t("lang.after_always"),
      ],
      onforPop: false,
      RemoteControlModeFlag: 1, //设备是不是可以远程控制标志位 0 不可控 1可控制
      oneZoneFlag: 0, //单温区还是三温区标志位 0 三温区 1单温区(只有A温区)
      sabbathmode: 0,
      sabbathModeStatus:0,
      // firstEnter: true,
      onlineStatus: '1',
      lightVal: null,
      gotoflag: 0,
      AmbientOntimeText: '',
      init_data:{},
      dragFlag:false
    };
  },
  created() {
    this.oneZoneFlag = this.$route.query.oneZoneFlag || 1; //单温区还是三温区标志位 0 三温区 1单温区(只有A温区)
    this.nativeKey = this.$route.name || "lights";
    this.$i18n.locale = this.$commonMethods.getNativeLanguage(
      this.$route.query.language,
      this.hasLanguage,
      this.lan
    );
    this.from = this.$route.query.from || "html";
    this.deviceId = this.$route.query.deviceId || "";
    this.onlineStatus = this.$route.query.onlineStatus ? this.$route.query.onlineStatus : "1";

    window.getLightsCallback = this.getLightsCallback;
    window.sendCmdCallback = this.sendCmdCallback;
    window.onBackPressedCallback = this.onBackPressedCallback;
    window.getDevicePropertiesInfoCallback = this.getDevicePropertiesInfoCallback;

    // this.getDevicePropertiesInfo()
    // this.showLoading = true
  },
  mounted() {
    if(window.sessionStorage.getItem('lightVal') != null){
      this.lightVal = JSON.parse(window.sessionStorage.getItem('lightVal'))
      if(this.lightVal != null){
        this.AmbientOntime = this.lightVal.AmbientOntime;
        if(this.AmbientOntime == 6){
          this.AmbientOntimeText = this.presenseSensorDisplayList[0];
        }else{
          this.AmbientOntimeText = this.presenseSensorDisplayList[this.AmbientOntime+1];
        }
        this.AmbientLightsIntensity = this.lightVal.AmbientLightsIntensity;
        if (this.AmbientLightsIntensity > 0) {
          this.lightsAmbientOnoff = true;
        } else {
          this.lightsAmbientOnoff = false;
        }
        this.AmbientLightsColour = this.lightVal.AmbientLightsColour
        this.AmbientRGBlightIntensity = this.lightVal.AmbientRGBlightIntensity
        if (this.AmbientRGBlightIntensity > 0) {
          this.RGBLightOnoff = true;
        } else {
          this.RGBLightOnoff = false;
        }

        this.oneZoneFlag = this.lightVal.oneZoneFlag
      }
    } else {
      this.getDevicePropertiesInfo()
      this.showLoading = true;
    }
  },
  activated() {
    // this.nativeKey = this.$route.name || "lights";
    // this.$i18n.locale = this.$commonMethods.getNativeLanguage(
    //   this.$route.query.language,
    //   this.hasLanguage,
    //   this.lan
    // );
    // this.from = this.$route.query.from || "html";
    // console.log("newLights this.from", this.from);
    // this.deviceId = this.$route.query.deviceId || "";
    // window.getLightsCallback = this.getLightsCallback;
    // window.sendCmdCallback = this.sendCmdCallback;
    // window.onBackPressedCallback = this.onBackPressedCallback;
    // window.getDevicePropertiesInfoCallback = this.getDevicePropertiesInfoCallback;
  },
  watch: {
    // $route(to,from){
    //   console.log(123,to,from)
    //   if(to.name == 'newLights' && from.name == 'colorSelect'){
    //     console.log(124,to,from)
    //     for(let key in this.$route.query){
    //       if(key == 'color'){
    //         this.AmbientColour = this.$route.query.color
    //         console.log(132,this.AmbientColour)
    //         this.$store.commit('lights/AMBIENT_COLOUR', this.AmbientColour)
    //       }
    //     }
    //   }
    // }
  },
  destroyed() {
    if(this.gotoflag == 0){
      window.sessionStorage.removeItem('lightVal')
    }
  },
  beforeRouteLeave(to, from, next) {
    console.log("to.name", to.name);
    console.log("from.name", from.name);
    if (to.name == "newLights" && from.name == "ambientMode") {
      from.meta.keepAlive = false;
      this.gotoflag = 0
    } else {
      from.meta.keepAlive = false;
      this.gotoflag = 1
      if(this.oneZoneFlag == 0){
        this.lightVal =  {
          AmbientLightsIntensity: this.AmbientLightsIntensity,
          AmbientLightsColour: this.AmbientLightsColour,
          AmbientRGBlightIntensity: this.AmbientRGBlightIntensity,
          AmbientOntime: this.AmbientOntime,
          oneZoneFlag: this.oneZoneFlag
        };
      } else {
        this.lightVal =  {
          AmbientLightsIntensity: this.AmbientLightsIntensity,
          AmbientLightsColour: this.AmbientLightsColour,
          AmbientOntime: this.AmbientOntime,
          oneZoneFlag: this.oneZoneFlag
        };
      }

      window.sessionStorage.setItem('lightVal', JSON.stringify(this.lightVal))
    }
    next();
  },
  computed: {
    commonHeaderBg() {
      return { background: "#fff" };
    },
    sliderBg() {
      return {
        background: "rgb(123,123,123)",
        background:
          "-webkit-linear-gradient(left, rgb(123,123,123) 0% , rgb(187,187,187) 42%, rgb(228,228,228) 71%, rgb(255,255,255) 100%)",
        background:
          "-o-linear-gradient(to right, rgb(123,123,123) 0% , rgb(187,187,187) 42%, rgb(228,228,228) 71%, rgb(255,255,255) 100%)",
        background:
          "-moz-linear-gradient(to right, rgb(123,123,123) 0% , rgb(187,187,187) 42%, rgb(228,228,228) 71%, rgb(255,255,255) 100%)",
        background:
          "linear-gradient(to right, rgb(123,123,123) 0% , rgb(187,187,187) 42%, rgb(228,228,228) 71%, rgb(255,255,255) 100%)",
      };
    },
    sliderBgSelect() {
      return {
        background: "rgb(0, 0, 0)",
        background:
          "-webkit-linear-gradient(left, rgb(0, 0, 0), rgb(233, 233, 233))",
        background:
          "-o-linear-gradient(to right, rgb(0, 0, 0), rgb(233, 233, 233))",
        background:
          "-moz-linear-gradient(to right, rgb(0, 0, 0), rgb(233, 233, 233))",
        background:
          "linear-gradient(to right, rgb(0, 0, 0), rgb(233, 233, 233))",
      };
    },
    sliderBgs() {
      return {
        background: "rgb(229,229,229)",
        background:
          "-webkit-linear-gradient(left, rgb(253,195,0) 0% , rgb(229,229,229) 52%, rgb(0,182,237) 100%)",
        background:
          "-o-linear-gradient(right, rgb(253,195,0) 0% , rgb(229,229,229) 52%, rgb(0,182,237) 100%)",
        background:
          "-moz-linear-gradient(right, rgb(253,195,0) 0% , rgb(229,229,229) 52%, rgb(0,182,237) 100%)",
        background:
          "linear-gradient(to right, rgb(253,195,0) 0% , rgb(229,229,229) 52%, rgb(0,182,237) 100%)",
      };
    },
    barHeight() {
      return "0.64rem";
    },
    _AmbientColour() {
      return this.$store.state.lights.AmbientColour;
    },
  },
  methods: {
    //获取设备属性信息
    getDevicePropertiesInfo() {
      let auidInfo = { deviceId: this.deviceId };
      // this.showLoading = true
      this.$native.getDevicePropertiesInfo(
        this.from,
        this.nativeKey,
        JSON.stringify(auidInfo)
      );
      if (this.from == "html") {
        let res = {
          resultCode: 0,
          data: {
            deviceNickName: "wineCooller1wineCooller2",
            deviceProps: {
              RemoteControlMode: "1",
              SabbathMode: '1',
              SabbathModeStatus:'2'
            },
          },
          errorInfo: "",
        };
        this.getDevicePropertiesInfoCallback(res);
      }
    },
    //获取设备属性列表回调
    getDevicePropertiesInfoCallback(res) {
      console.log("获取设备属性列表回调的callBack", JSON.stringify(res));
      // if(this.firstEnter){
      //   this.firstEnter = false
        // this.showLoading = false;
        this.proList = res;
        if (this.proList.resultCode == "0" || this.proList.resultCode == 0) {
          if ("data" in this.proList && "deviceProps" in this.proList.data) {
            let { deviceProps, deviceNickName } = this.proList.data;
            this.sabbathmode = Number(deviceProps.SabbathMode)
            this.sabbathModeStatus = Number(deviceProps.SabbathModeStatus)
            this.getLightsInfo();
          }
          // else if("data" in this.proList && this.proList.data.length == 0){
          //   this.getLightsInfo();
          // }
        } else {
          this.$toast(this.$t("lang.failure"));
          this.showLoading = false;
        }
      // }
    },
    toToast() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      } else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        console.log("可以控制");
      }
    },
    showPopSave() {
      this.showPop = false;
    },
    onConfirm(value, index) {
      this.AmbientOntimeText = value;
      if(index == 0){
        this.AmbientOntime = index + 6;
      }else{
        this.AmbientOntime = index - 1;
      }
      console.log("AmbientOntime的值",this.AmbientOntime);
      this.showPop = false;
      this.onforPop = false;
    },
    onCancel() {
      this.showPop = false;
      this.onforPop = false;
    },
    togglePop() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      } else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      }else {
        console.log("可以控制");
        this.showPop = true;
        this.onforPop = true;
      }
    },
    //返回
    goBack() {
      let params = {
        language: this.$i18n.locale,
        from: this.from,
        deviceId: this.deviceId,
      };
      this.$commonMethods.routerGo(this.$router, "/newLights/newLights", params);
    },

    // switch开关 shelfLights brightness
    shelfLightsSwitch(val) {
      // if (!val) {
      //   this.AmbientLightsIntensity = 0;
      //   // this.toSaveSwitchLightsIntensity()
      // } else {
      // }
    },
    // 滑动灯架亮度shelfLights brightness
    handleShelfLightsBrightness(val) {
      console.log('handleShelfLightsBrightness',val)
      if(this.onlineStatus == "2"|| this.RemoteControlModeFlag == 0 || (this.sabbathmode == 1 && this.sabbathModeStatus == 2)){
        if(this.onlineStatus == "2"){
          this.$toast(this.$t("lang.appliance_offline"));
        }else if (this.RemoteControlModeFlag == 0) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
        } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
        }
        this.AmbientLightsIntensity  = Number(this.init_data.AmbientLightsIntensity)
         this.getLightsInfo();
      } else {
        if(!this.dragFlag) this.$fireBase.gaWebReport(this.from,'click','ambient_mode_shelf_lights_brightness', {"AmbientLightsIntensity":val})
        this.AmbientLightsIntensity = val
        // if (val > 0) {
        //   this.lightsAmbientOnoff = true;
        // } else {
        //   this.lightsAmbientOnoff = false;
        // }
      }
    },
    handleShelfLightsBrightnessDrag(){
      this.$fireBase.gaWebReport(this.from,'move','ambient_mode_shelf_lights_brightness', {"AmbientLightsIntensity":this.AmbientLightsIntensity})
      this.dragFlag = false
    },
    //light tempreature
    handleLightsTemperature(val) {
      if(this.onlineStatus == "2"|| this.RemoteControlModeFlag == 0 || (this.sabbathmode == 1 && this.sabbathModeStatus == 2)){
        if(this.onlineStatus == "2"){
          this.$toast(this.$t("lang.appliance_offline"));
        }else if (this.RemoteControlModeFlag == 0) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
        } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
        }
        this.AmbientLightsColour  = Number(this.init_data.AmbientLightsColour)
         this.getLightsInfo();
      } else {
        if(!this.dragFlag) this.$fireBase.gaWebReport(this.from,'click','ambient_mode_shelf_lights_lights_temperature',{"AmbientLightsColour":val})
        this.AmbientLightsColour = val;
      }
    },
    handleLightsTemperatureDrag(){
      this.$fireBase.gaWebReport(this.from,'move','ambient_mode_shelf_lights_lights_temperature', {"AmbientLightsColour":this.AmbientLightsColour})
      this.dragFlag = false
    },
    // RBG切换开关
    RGBlightsSwitch(val) {
      // if (!val) {
      //   this.AmbientRGBlightIntensity = 0;
      // } else {
      // }
    },
    //RGB slider
    handleRGBLightBrightness(val) {
      if(this.onlineStatus == "2"|| this.RemoteControlModeFlag == 0 || (this.sabbathmode == 1 && this.sabbathModeStatus == 2)){
        if(this.onlineStatus == "2"){
          this.$toast(this.$t("lang.appliance_offline"));
        }else if (this.RemoteControlModeFlag == 0) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
        } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
          this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
        }
        this.AmbientRGBlightIntensity  = Number(this.init_data.AmbientRGBlightIntensity)
        this.getLightsInfo();
      } else {
        if(!this.dragFlag) this.$fireBase.gaWebReport(this.from,'click','ambient_mode_rgb_light_brightness',{"AmbientRGBlightIntensity":val})
        this.AmbientRGBlightIntensity = val
        // if (val > 0) {
        //   this.RGBLightOnoff = true;
        // } else {
        //   this.RGBLightOnoff = false;
        // }
      }
    },
    handleRGBLightBrightnessDrag(){
      this.$fireBase.gaWebReport(this.from,'move','ambient_mode_rgb_light_brightness', {"AmbientRGBlightIntensity":this.AmbientRGBlightIntensity})
      this.dragFlag = false
    },
    //跳转颜色选择界面
    selectColor() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      }else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      } else {
        let params = {
          language: this.$i18n.locale,
          from: this.from,
          deviceId: this.deviceId,
          fromPageName: "ambientMode",
        };
        this.$commonMethods.routerGo(this.$router, "/newLights/colorSelect", params);
      }
    },
    //获取灯光信息
    getLightsInfo() {
      // this.showLoading = true;
      let type = { deviceId: this.deviceId };

      //刷新数据
      // if(this.from=='ios'){
      //   window.webkit.messageHandlers.getDevicePropertiesInfo.postMessage('')
      // }else if(this.from=='android'){
      //   window.wine.getDevicePropertiesInfo(this.nativeKey, JSON.stringify(type))
      // }

      this.$native.getLightsInfo(
        this.from,
        this.nativeKey,
        JSON.stringify(type)
      );
      if (this.from == "html") {
        let lightsInfo = {
          errorDesc: "获灯光信息成功",
          resultCode: "0",
          data: {
            AmbientLightsIntensity: "1", //1 brightness
            AmbientLightsColour: "20",
            AmbientRGBlightIntensity: "0",
            AmbientRcolour: "125",
            AmbientGcolour: "125",
            AmbientBcolour: "125",
            AmbientOntime: "6",
            RemoteControlMode: "1",
          },
        };
        this.getLightsCallback(lightsInfo);
      }
    },
    //获取灯光信息回调
    getLightsCallback(val) {
      console.log("ambient getLightsCallback返回的数据", JSON.stringify(val));
      // console.log(val)

      if (val.resultCode == "0" || val.resultCode == 0) {
        if ("data" in val) {
          let lightsInfo = val.data;
          if(JSON.stringify(this.init_data)=="{}"){
            //v12用
            this.AmbientOntime = Number(lightsInfo.AmbientOntime) || 0;
            if(this.AmbientOntime == 6){
              this.AmbientOntimeText = this.presenseSensorDisplayList[0];
            }else{
              this.AmbientOntimeText = this.presenseSensorDisplayList[this.AmbientOntime+1];
            }
            this.AmbientLightsIntensity = Number(lightsInfo.AmbientLightsIntensity) || 0;
            if (this.AmbientLightsIntensity > 0) {
              this.lightsAmbientOnoff = true;
            } else {
              this.lightsAmbientOnoff = false;
            }

            if(this.oneZoneFlag == 0){//三温区
              this.AmbientLightsColour = 23 - Number(lightsInfo.AmbientLightsColour) || 0;
            }else{
              this.AmbientLightsColour = Number(lightsInfo.AmbientLightsColour) || 0;
            }
            this.AmbientRGBlightIntensity = Number(lightsInfo.AmbientRGBlightIntensity) || 0;
            if (this.AmbientRGBlightIntensity > 0) {
              this.RGBLightOnoff = true;
            } else {
              this.RGBLightOnoff = false;
            }
            this.AmbientRColour = Number(lightsInfo.AmbientRcolour) || 0;
            this.AmbientGColour = Number(lightsInfo.AmbientGcolour) || 0;
            this.AmbientBColour = Number(lightsInfo.AmbientBcolour) || 0;
            const AmbientColour ="rgb(" + this.AmbientRColour + "," + this.AmbientGColour + "," + this.AmbientBColour + ")";
            // console.log(AmbientColour)
            this.$store.commit("lights/AMBIENT_R_COLOUR", this.AmbientRColour);
            this.$store.commit("lights/AMBIENT_G_COLOUR", this.AmbientGColour);
            this.$store.commit("lights/AMBIENT_B_COLOUR", this.AmbientBColour);
            this.$store.commit("lights/AMBIENT_COLOUR", AmbientColour);
            console.log('氛围灯RGB颜色',AmbientColour)
            console.log('_AmbientColour{}氛围灯RGB颜色',this._AmbientColour)
            this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
            this.init_data = lightsInfo;
          }
          if(this.init_data.AmbientOntime != Number(lightsInfo.AmbientOntime)){
            //v12用
            this.init_data.AmbientOntime = Number(lightsInfo.AmbientOntime)
            this.AmbientOntime = Number(lightsInfo.AmbientOntime) || 0;
            if(this.AmbientOntime == 6){
              this.AmbientOntimeText = this.presenseSensorDisplayList[0];
            }else{
              this.AmbientOntimeText = this.presenseSensorDisplayList[this.AmbientOntime+1];
            }
          }
          if(this.init_data.AmbientLightsIntensity != Number(lightsInfo.AmbientLightsIntensity)){
            this.init_data.AmbientLightsIntensity = Number(lightsInfo.AmbientLightsIntensity)
            this.AmbientLightsIntensity =
              Number(lightsInfo.AmbientLightsIntensity) || 0;
            if (this.AmbientLightsIntensity > 0) {
              this.lightsAmbientOnoff = true;
            } else {
              this.lightsAmbientOnoff = false;
            }
          }
          if(this.init_data.AmbientLightsColour != Number(lightsInfo.AmbientLightsColour)){
            this.init_data.AmbientLightsColour = Number(lightsInfo.AmbientLightsColour)
            if(this.oneZoneFlag == 0){//三温区
              this.AmbientLightsColour = 23 - Number(lightsInfo.AmbientLightsColour) || 0;
            }else{
              this.AmbientLightsColour = Number(lightsInfo.AmbientLightsColour) || 0;
            }
          }
          if(this.init_data.AmbientRGBlightIntensity != Number(lightsInfo.AmbientRGBlightIntensity)){
            this.init_data.AmbientRGBlightIntensity = Number(lightsInfo.AmbientRGBlightIntensity)
            this.AmbientRGBlightIntensity = Number(lightsInfo.AmbientRGBlightIntensity) || 0;
            if (this.AmbientRGBlightIntensity > 0) {
              this.RGBLightOnoff = true;
            } else {
              this.RGBLightOnoff = false;
            }
          }
          if(this.init_data.AmbientRcolour != Number(lightsInfo.AmbientRcolour)){
            this.init_data.AmbientRcolour = Number(lightsInfo.AmbientRcolour)
            this.AmbientRColour = Number(lightsInfo.AmbientRcolour) || 0;
            this.$store.commit("lights/AMBIENT_R_COLOUR", this.AmbientRColour);
          }
          if(this.init_data.AmbientGcolour != Number(lightsInfo.AmbientGcolour)){
            this.init_data.AmbientGcolour = Number(lightsInfo.AmbientGcolour)
            this.AmbientGColour = Number(lightsInfo.AmbientGcolour) || 0;
            this.$store.commit("lights/AMBIENT_G_COLOUR", this.AmbientGColour);

          }
          if(this.init_data.AmbientBcolour != Number(lightsInfo.AmbientBcolour)){
            this.init_data.AmbientBcolour = Number(lightsInfo.AmbientBcolour)
            this.AmbientBColour = Number(lightsInfo.AmbientBcolour) || 0;
            this.$store.commit("lights/AMBIENT_B_COLOUR", this.AmbientBColour);
          }
          if(this.init_data.RemoteControlModeFlag != Number(lightsInfo.RemoteControlModeFlag)){
            this.init_data.RemoteControlModeFlag = Number(lightsInfo.RemoteControlModeFlag)
            this.RemoteControlModeFlag = Number(lightsInfo.RemoteControlMode);
          }
          console.log('AmbientRColour',this.AmbientRColour)
          console.log('AmbientGColour',this.AmbientGColour)
          console.log('AmbientBColour',this.AmbientBColour)
          const AmbientColour ="rgb(" + this.AmbientRColour + "," + this.AmbientGColour + "," + this.AmbientBColour + ")";
          console.log('氛围灯RGB颜色',AmbientColour)
          this.$store.commit("lights/AMBIENT_COLOUR", AmbientColour);
          console.log('_AmbientColour氛围灯RGB颜色',this._AmbientColour)

        }
      } else {
        this.$toast(this.$t("lang.failure"));
      }

      this.showLoading = false;
    },
    saveHandle() {
      if(this.onlineStatus == "2"){
        this.$toast(this.$t("lang.appliance_offline"));
      } else if (this.RemoteControlModeFlag == 0) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_on_the_remote")+`</span></div>`,type: 'html' });
      } else if (this.sabbathmode == 1 && this.sabbathModeStatus == 2) {
        this.$toast({message: `<div style="word-break:keep-all;word-wrap:break-word;overflow:hidden;"><span>`+this.$t("lang.turn_off_the_sabbath")+`</span></div>`,type: 'html' });
      }else {
        let params = {}
        if(this.oneZoneFlag == 0){
          params =  {
            AmbientLightsIntensity: this.AmbientLightsIntensity,
            AmbientLightsColour: 23 - Number(this.AmbientLightsColour),
            AmbientRGBlightIntensity: this.AmbientRGBlightIntensity,
            AmbientOntime: this.AmbientOntime,
          };
        } else {
          params =  {
            AmbientLightsIntensity: this.AmbientLightsIntensity,
            // AmbientLightsColour: 23 - Number(this.AmbientLightsColour),
            AmbientLightsColour: Number(this.AmbientLightsColour),
            AmbientOntime: this.AmbientOntime,
          };
        }
        this.$fireBase.gaWebReport(this.from,'save_btn','ambient_mode_save',params)
        console.log("点击保存的入参params",params);
        this.$native.sendCmd(
          this.from,
          this.nativeKey,
          this.deviceId,
          JSON.stringify(params)
        );
        if (this.from == "html") {
          let res = {
            resultCode: "0",
          };
          this.sendCmdCallback(res);
        }
      }
    },
    sendCmdCallback(data) {
      // console.log("sendCmdCallback返回数据", data);
      this.showLoading = false;
      console.log("sendCmdCallback返回数据_ambient页面", data);
      if (data.resultCode == "0" || data.resultCode == 0) {
        // alert("ambient1")
        this.$toast(this.$t("lang.success"));
        let params = {
          language: this.$i18n.locale,
          from: this.from,
          deviceId: this.deviceId,
        };
        this.$commonMethods.routerGo(this.$router, "/newLights/newLights", params);
      } else {
        this.showLoading = false;
        this.$toast(this.$t("lang.failure"));
      }
    },
    //物理返回键
    onBackPressedCallback() {
      this.goBack();
    },
  },
  components: {
    commonHeader: () => import("@/components/common/commonHeader"),
    commonBottom: () => import("@/components/common/commonBottom"),
    loading: () => import("@/components/common/loading"),
  },
};
</script>
<style lang="less" scoped>
