  overflow: hidden;
  cursor: pointer;
  i {
    display: block;
    margin: 14px auto 14px 20px;
    font-size: 16px;
    // font-family: Arial;
  }
}
#wine_lights .common_header_center {
  position: absolute;
  top: 0;
  left: 19%;
  width: 80%;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  color: #000;
  // font-family: Arial;
}
/* 主体 */
/* 主体 */
#wine_lights .lights_tab_card_content {
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  bottom: 0px;
  padding-top: 40px;
  color: #000;
  font-size: 16px;
  // font-family: Arial;
  background: #e9e9e9;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  .lights_content {
    width: 100%;
    overflow: hidden;
    .lights_switch {
      width: 83%;
      height: 39px;
      margin: 0 auto 10px;
      .lights_switch_title {
        float: left;
        width: 80%;
        height: 39px;
        line-height: 39px;
        font-size: 20px;
        // font-family: Arial;
        color: #000;
        font-weight: bold;
      }
      .lights_switch_btn {
        float: right;
        width: 17%;
        height: 39px;
        padding-top: 4px;
      }
    }
    .lights_item {
      width: 83%;
      margin: 10px auto 0;
      .lights_title {
        width: 100%;
        margin-bottom: 19px;
