        color: #000;
        // font-family: Arial;
        font-weight: 800;
      }
      .lights_bright_percent {
        text-align: right;
        width: 100%;
        margin-bottom: 10px;
        font-size: 18px;
        height: 20px;
        line-height: 20px;
        font-weight: 800;
        // font-family: Arial;
      }
      .lights_bright_percent_show {
        display: flex;
        margin-top: 5px;
        justify-content: space-between;
        align-items: center;
        span {
          font-size: 10px;
          font-weight: 700;
          // font-family: Arial;
        }
      }
      .lights_disable_block {
        width: 100%;
        height: 24px;
        margin: 0 auto;
        background: #c4c4c4;
        opacity: 0.3;
        filter: alpha(opacity=30);
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
      }
      .lights_time {
        width: 100%;
        font-size: 16px;
        // font-family: Arial;
      }
      .ambient_color {
        width: 100%;
        height: 24px;
        margin: 0 auto;
        -webkit-border-radius: 12px;
        -moz-border-radius: 12px;
        -ms-border-radius: 12px;
        -o-border-radius: 12px;
        border-radius: 12px;
        cursor: pointer;
      }
    }
    .lights_item_ambient {
      margin: 24px auto 0;
    }
  }
}
#wine_lights .lights_slider_wrap {
  width: 100%;
  height: 24px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  border-radius: 12px;
}
